import { Directive, ElementRef } from '@angular/core';

declare var jQuery: any;
declare var $: any;

@Directive({
  selector: '[efSlider]'
})
export class SliderDirective {

  constructor(private elementRef: ElementRef) { 
    jQuery(elementRef.nativeElement).slider({
      slide: (event, ui) => {
        console.log(ui.value);
      }
    });
  }

}
